.stages-menu__wrapper {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;

	.menu__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
	}

	.stages-menu__block{
		display: flex;
		align-items: center;
	}
	
	.menu__item{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 5px;
	}

	.menu__item--active{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 5px;
		width: 100%;
		max-width: 204px;
	}

	.menu__item-button--active{
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		max-width: 204px;
		margin-right: 5px;
	}

	.menu__item-button--inactive{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 5px;
	}
	
	.menu__item-icon{
		height: 30px;
		width: 35px;
	}
	
	.stages-menu__block-button-addition{
		border: 1px solid var(--palette-live-grey-7);
		background-color: var(--palette-live-bg-0-white);
		height: 42px;
		border-radius: 14px;
		width: 42px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 40px;
		font-weight: 200;
		color: var(--palette-live-grey-7);
		margin-right: 10px;
	}

	.menu__container {
		width: 100%;
		display: flex;
		justify-content: left;
		align-items: center;
	}

	.menu__item-button--active{
		border: 1px solid var(--palette-live-blue);
		background-color: var(--palette-live-blue);
		color: white;
		height: 42px;
		border-radius: 14px;
		width: 42px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 23px;
		margin-right: 5px;
		cursor: pointer;
	}

	.menu__item-button--inactive {
		background-color: var(--palette-live-bg-0-white);
		color: var(--palette-live-grey-7);
		border: 1px solid var(--palette-live-grey-7);
		height: 42px;
		border-radius: 14px;
		width: 42px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 23px;
		margin-right: 5px;
		cursor: pointer;
	}

	.menu__item-options {
		border: 1px solid var(--palette-live-grey-7);
		padding: 3px;
		border-radius: 14px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 210px;
		width: 100%;
	}

	.menu__item-options-button {
		border: 1px solid var(--palette-live-blue);
		background-color: var(--palette-live-blue);
		color: white;
		height: 36px;
		border-radius: 12px;
		width: 36px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 23px;
		cursor: pointer;
	}

	.menu__item-options-icon{
		margin-right: 5px;
		cursor: pointer;
	}
}