.stage-plug__wrapper{
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: auto 0;

	.stage-plug__container{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 100px 0;

		.stage-plug__title{
			display: block;
			text-align: center;
			margin: 20px 0;
			font-size: 24px;
			font-weight: 500;
		}

		.stage-plug__subtitle{
			font-size: 18px;
			display: block;
			text-align: center;
		}
	}
}