.header__container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px 20px 15px 20px;
	border: solid var(--palette-live-grey-5);
	border-top-width: 0;
	border-left-width: 0;
	border-right-width: 0;
	background-color: var(--palette-live-bg-0-white);

	.header__button-container {
		width: 42px;
		height: 42px;
	}

	.header__section {
		display: flex;
		flex-direction: column;
		align-items: center;

		.header__community-name {
			color: var(--palette-live-grey-2);
			font-size: 18px;
			margin: 0 0 15px 0;
			font-weight: 400;
		}
	
		.header__title {
			color: var(--palette-live-grey-1);
			font-size: 20px;
			margin: 0 0 0 0;
			font-weight: 700;
			text-align: center;
		}
	}

	.header__button-container {
		width: 42px;
		height: 42px;
	}
}