.bottom-menu__wrapper{
	z-index: 9998;
	position: fixed;
	bottom: 0;
	width: 100%;

	.bottom-menu__container {
		background-color: var(--palette-live-bg-0-white);
		padding: 12px 20px 12px 20px;
		display: flex;
		justify-content: space-between;
		position: relative;
	
		.bottom-menu__block {
			display: flex;
			flex-direction: column;
			align-items: center;
			cursor: pointer;
			background-color: var(--palette-live-bg-0-white);
			border: 0;
			padding: 0;
			margin: 0;
	
			.tennis-icon {
				width: 16px;
				height: 21px;
				fill: var(--palette-live-grey-6);
			}
	
			.users-icon {
				width: 21px;
				height: 21px;
				fill: var(--palette-live-grey-6);
			}
	
			.communities-icon {
				width: 21px;
				height: 21px;
				fill: var(--palette-live-grey-6);
			}
	
			.account-icon {
				width: 20px;
				height: 20px;
				fill: var(--palette-live-grey-6);
			}
	
			.block-name {
				font-size: 11px;
				margin: 3px 0 0 0;
				color: var(--palette-live-grey-6);
			}
	
			.change-color-icon {
				fill: var(--palette-live-blue);
			}
	
			.change-color-name {
				color: var(--palette-live-blue);
			}
		}
	}
}