.olympicplus-grid__container {
  overflow: auto;
  margin-bottom: 20px;
}

.olympicplus-grid__title {
  font-size: 20px;
  font-weight: 500;
  margin: 10px 0;
}

.olympicplus-grid__table-wrapper {
  overflow: auto;
  overflow-y: hidden;
  width: 100%;
  display: flex;

  .olympicplus-grid__table-container {
    box-sizing: border-box;
    border-top: 1px solid var(--palette-live-grey-6);
    border-bottom: 1px solid var(--palette-live-grey-6);
    border-left: 1px solid var(--palette-live-grey-6);
    padding-right: 3px;
  }

  .olympicplus-grid__table-container:first-child {
    border-top-left-radius: 15px;

  }

  .olympicplus-grid__table-container:last-child {
    border-top-right-radius: 15px;
    border-right: 1px solid var(--palette-live-grey-6);
    padding: 0;

    .olympicplus-grid__cell, .olympicplus-grid__table {
      border-right: none;
    }
  }

  .olympicplus-grid__table {
    display: table;
    width: max-content;
    border-right: 1px solid var(--palette-live-grey-6);
  }
}

.olympicplus-grid__row,
.olympicplus-grid__row-header {
  display: table-row;
}

.olympicplus-grid__row-header {
  height: 50px;
  position: relative;
}

.olympicplus-grid__header {
  position: absolute;
  margin: 0;
  padding: 0 15px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

.olympicplus-grid__cell {
  display: table-cell;
  border-top: 1px solid var(--palette-live-grey-6);
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  vertical-align: middle;
  min-width: 45px;
  text-align: center;
  height: 60px;
  box-sizing: border-box;
}

.olympicplus-grid__match {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  flex-direction: row;
}

.olympicplus-grid__match-continer {
  flex-grow: 1;
  display: flex;
  align-items: center;
  margin-right: 20px;
  height: 100%;
  width: 100%;
}

.olympicplus-grid__match-order {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 85%;
  width: 22px;
  margin: 0 5px 0 0;
  border-right: 1px solid var(--palette-live-grey-6);
}

.olympicplus-grid__match-number {
  margin: 0;
  font-size: 12px;
  color: var(--palette-live-grey-7);
}

.olympicplus-grid__match-ball {
  transform: scale(1);
  margin-top: 3px;
}

.olympicplus-grid__match-participant,
.olympicplus-grid__match-dobles {
  margin: 0;
  text-align: left;
}

.olympicplus-grid__match-participant{
  height: 20px;
  display: flex;
  align-items: center;
}

.olympicplus-grid__match-dobles{
  margin-bottom: 10px;
  height: 45px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.olympicplus-grid__match-dobles:last-child{
  margin-bottom: 0;
}

.olympicplus-grid__match-double-player {
  margin: 0 0 5px 0;
}

.olympicplus-grid__match-double-player:last-child{
  margin: 0;
}

.olympicplus-grid__match-noplayer{
  margin: 0;
}

.olympicplus-grid__match-participant:first-child {
  margin-bottom: 5px;
}

.olympicplus-grid__match-participant--empty{
  display: block;
  height: 1.5px;
  width: 50px;
  background-color: var(--palette-live-grey-9);
  box-sizing: border-box;
}

.olympicplus-grid__match-loser{
  color: var(--palette-live-grey-9);
}

.olympicplus-grid__cell-sets {
  display: table-cell;
  border-top: 1px solid var(--palette-live-grey-6);
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  vertical-align: middle;
  text-align: center;
  height: 60px;
  box-sizing: border-box;
  padding-right: 10px;
}

.olympicplus-grid__match-sets {
  display: flex;
  justify-content: flex-start;
  height: 100%;
}

.olympicplus-grid__match-set {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  padding: 8px 0;
}

.olympicplus-grid__match-set-points {
  display: flex;
  align-items: center;
}

.olympicplus-grid__match-set-score {
  color: var(--palette-live-blue);
  font-family: 'Sawarabi Gothic';
  margin: 0;
  width: 10px;
}

.olympicplus-grid__match-set-tiebreak {
  color: var(--palette-live-blue);
  font-family: 'Sawarabi Gothic';
  margin: 0;
  font-size: 11px;
  width: 10px;
}

.olympicplus-grid__result-container {
  height: 30px;
  padding: 0;
  min-width: 60px;
}

.olympicplus-grid__result {
  display: flex;
  width: 100%;
  justify-content: space-between;
  height: 100%;
}

.olympicplus-grid__result-order,
.olympicplus-grid__result-double {
  margin: 0;
  font-size: 17px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.olympicplus-grid__result-double{
  padding: 0 5px;
  box-sizing: border-box;
}

.olympicplus-grid__result-name {
  min-width: 60px;
  margin: auto 0;
  padding: 0 10px;
}

.olympicplus-grid__result-order{
  width: 30px;
  background-color: var(--palette-live-grey-0);
  color: var(--palette-live-bg-0-white);
  align-items: center;
}