.tournaments-block__container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--palette-live-bg-0-white);
	padding: 15px;
	border: solid var(--palette-live-grey-5);
	border-top-width: 0;
	border-right-width: 0;
	border-bottom-width: 1px;
	border-left-width: 0;
	cursor: pointer;

	.tournaments-block__date {
		display: flex;
		flex-direction: column;
		align-items: center;

		.tournaments-date__element {
			color: var(--palette-live-grey-0);
			font-size: 14px;
			margin: 0 0 0 0;
			font-weight: 400;

			&:nth-of-type(2) {
				margin: 0;
				font-weight: 600;
				font-size: 24px;
			}
		}
	}

	.tournaments-block__data {
		box-sizing: border-box;
		width: 80%;
		padding: 0 0 0 15px;

		.tournaments-block__name {
			color: var(--palette-live-grey-0);
			font-size: 17px;
			margin: 0;
			font-weight: 600;
			box-sizing: border-box;
			overflow-wrap: break-word;
		}

		.tournaments-block__description {
			color: var(--palette-live-grey-0);
			font-size: 14px;
			margin: 7px 0 7px 0;
			font-weight: 400;
		}

		.tournaments-block__addres {
			color: var(--palette-live-grey-0);
			font-size: 14px;
			margin: 0 0 0 0;
			font-weight: 400;
		}
	}

	.tournaments-bubble__container {
		margin: 0 10px 0 0;

		.tournaments-bubble {
			width: 20px;
			height: 20px;
			background-color: #CDFF40;
			border-radius: 100px;
		}
	}

	.arrow-icon {
		width: 24px;
		height: 24px;
		flex-shrink: 0;
		transform: rotate(180deg);
	}
}