.community__container {
	height: 100%;
	padding: 0 20px 0 20px;

	a:last-child {
		margin-top: 15px;
	}

	.community__input-name {
		box-sizing: border-box;
		padding: 20px;
		font-size: 18px;
		width: 100%;
		background-color: var(--palette-live-grey-8);
		border: 1px solid var(--palette-live-grey-5);
		outline: none;
	}

	.community__popup-buttons {
		display: flex;
		justify-content: space-between;
		width: inherit;
		padding-top: 20px;

		button {
			margin: 0;
		}

		button:first-child {
			margin-right: 5px;
		}

		button:last-child {
			margin-left: 5px;
		}
	}

	.radio-button__container:first-child{
		border-top: 1px solid var(--palette-live-grey-5);
	}
}