.input__container {
	margin: 0 0 16px 0;

	.input__block {
		margin: 0 0 0 0;
		border: 1px solid var(--palette-live-grey-5);
		border-radius: 10px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: var(--palette-live-bg-3);
		padding: 0 16px 0 0;

		.input__wrapper {
			width: 100%;
			height: 72px;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: space-between;

			.input__element {
				width: 100%;
				height: 72px;
				color: var(--palette-live-grey-1);
				font-size: 18px;
				margin: 0 0 0 0;
				padding: 26px 0 0 16px;
				border: 0;
				box-sizing: border-box;
				outline: none;
				border-top-left-radius: 9px;
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-bottom-left-radius: 9px;
				background-color: var(--palette-live-bg-3);

				&--focus {
					-webkit-box-shadow: 0 0 0 100px var(--palette-live-bg-0-white) inset !important;
				}
			}

			.input-label {
				position: absolute;
				pointer-events: none;
				left: 16px;
				top: unset;
				font-size: 18px;
				white-space: nowrap;
				overflow: hidden;
				transition: .3s;
				color: var(--palette-live-grey-3);
			}

			.input__element:focus~.input-label,
			.input__element:not(:focus):valid~.input-label,
			.input__element:autofill~.input-label {
				top: 14px;
				font-size: 16px;
			}
		}

		.visibility__icon {
			width: 30px;
			height: 30px;
			flex-shrink: 0;
			margin: 0 0 0 16px;
			fill: var(--palette-live-grey-3);
			cursor: pointer;
		}

		.edit-pen__icon {
			width: 24px;
			height: 24px;
			flex-shrink: 0;
			margin: 0 0 0 16px;
			fill: var(--palette-live-grey-0);
			cursor: pointer;
		}

		&--error {
			border-color: var(--palette-live-red-1);
		}

		&--focus {
			background-color: var(--palette-live-bg-0-white);
			border-color: var(--palette-live-grey-1);
		}
	}

	.error__input {
		color: var(--palette-live-red-1);
		font-size: 14px;
		margin: 10px 0 0 0;
	}
}