.group-player__single-container, .group-player__double-container {
  display: flex;
  align-items: center;
  padding: 0 5px;
  overflow:  hidden;
  
  p {
    margin: 5px 10px 5px 0 ;
  }
}

.group-player__number {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: #EBF4FF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-right: 15px;
}

.group-player__single-name {
  width: 100%;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  height: 50px;
}

.group-player__double-rating {
  font-size: 20px;
  width: 65px;
}

.group-player__double {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 64px;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-right: 10px;
  height: 65px;
}

.group-player__double-noplayer {
  height: 60px;
  font-size: 20px;
  display: flex;
  align-items: center;
}

.group-player__double-name {
  width: 100%;
  font-size: 20px;
  height: 30px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-right: 10px;
}