.roundrobin-grid__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.roundrobin-grid__title {
  color: #3A3A3A;
  font-size: 20px;
  margin: 0;
  font-weight: 500;
}

.roundrobin-grid__wrapper {
  margin: 0 0 40px 0;
}

.roundrobin-grid__sheet {
  overflow: auto;
  overflow-y: hidden;
  width: 100%;
  display: flex;
}

.roundrobin-grid__sheet::-webkit-scrollbar {
  width: 0;
}

.roundrobin-grid__content {
  display: table;
  border-collapse: collapse;
  width: max-content;
}

.roundrobin-grid__content--sticky {
  display: table;
  border-spacing: 0;
  position: sticky;
  left: 0;
  z-index: 9;
  background-color: var(--palette-live-bg-0-white);
  overflow: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

.roundrobin-grid__row {
  display: table-row;
  height: 64.5px;
}

.row-header {
  height: 49.5px;
}

.empty {
  display: table-row;
  height: inherit;
  width: 3px;
  min-width: 3px;
  border-top: 1px solid var(--palette-live-grey-6);
  border-bottom: 1px solid var(--palette-live-grey-6);
}

.roundrobin-grid__header {
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 500;
  padding: 0 10px;
  display: table-cell;
  border: 1px solid var(--palette-live-grey-6);
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  height: inherit;
  position: relative;
}

.group-name__container {
  border-top-left-radius: 15px;
  text-align: left;
  font-size: 20px;
  font-weight: 600;
  padding: 0 10px;
}

.roundrobin-grid__header--inner {
  font-size: 14px;
  padding-left: 5px;
}

.roundrobin-grid__cell {
  display: table-cell;
  border: 1px solid var(--palette-live-grey-6);
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  vertical-align: middle;
  min-width: 45px;
  height: inherit;
  position: relative;
  text-align: center;
}

.result-cell {
  background-color: #3A3A3A;
  color: var(--palette-live-bg-0-white);
  border-bottom: 1px solid var(--palette-live-grey-6);
  width: 30px;
  padding: 0;
  cursor: pointer;
}

.roundrobin-grid__cell-match-result {
  padding: 0 7px;
  white-space: nowrap;
}

.roundrobin-grid__cell--double {
  display: table-cell;
  border: 1px solid var(--palette-live-grey-6);
  vertical-align: middle;
  height: inherit;
}

.roundrobin-grid__cell-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.roundrobin-grid__cell--inner {
  border-right: 1px solid var(--palette-live-grey-6);
  color: #3A3A3A;
  font-size: 17px;
  font-weight: 400;
  white-space: nowrap;
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65px;
}

.roundrobin-grid__cell--inner:last-child {
  border-right: none;
}

.result-cell__score {
  font-family: 'Sawarabi Gothic';
}

.result-cell__score:last-child {
  color: var(--palette-live-blue-2);
}

.roundrobin-grid__cell--grey {
  background-color: var(--palette-live-grey-4);
}

.participant-name__container {
  border-top: none;
  border-left: 1px solid var(--palette-live-grey-6);
  border-right: 1px solid var(--palette-live-grey-6);
  border-bottom: 1px solid var(--palette-live-grey-6);
  position: relative;
  padding-left: 15px;
  white-space: nowrap;
  overflow: auto;
  min-width: 45px;
  max-width: 115px;
}

.roundrobin-grid__row:last-child .participant-name__container {
  border-bottom-left-radius: 15px;
}

.participant-name__order {
  position: absolute;
  top: 2px;
  left: 2px;
  color: var(--palette-live-grey-7);
  font-size: 14px;
  font-weight: 400;
}

.roundrobin-grid__icon-ball {
  transform: scale(0.9);
  border-radius: 100%;
  position: absolute;
  top: 2px;
  left: 2px;
}

.roundrobin-grid__cell-score {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-family: 'Sawarabi Gothic';
}

.roundrobin-grid__cell-score p {
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  margin: 0;
  padding: 0 5px;
}

.roundrobin-grid__cell-score p:first-child {
  padding-left: 10px;
}

.roundrobin-grid__cell-score p:last-child {
  padding-right: 10px;
}

.roundrobin-grid__cell-score>p>span {
  display: flex;
  justify-content: center;
  color: var(--palette-live-blue);
  white-space: nowrap;
}

.participant-name__item {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.participant-name__item>span {
  padding: 5px 0 0px 0;
}

.participant-name__item>span:last-child {
  padding: 0 0 5px 0;
}

.roundrobin-grid__popup-item-container {
  width: 100%;
  border-top: 1px solid var(--palette-live-grey-6);
  display: flex;
  justify-content: space-between;
  align-items: center;

  svg {
    padding: 10px;
  }
}

.roundrobin-grid__popup-item-container:last-child {
  border-bottom: 1px solid var(--palette-live-grey-6);
}

.roundrobin-grid__popup-item {
  display: flex;
  align-items: center;
  padding: 15px;
  color: var(--palette-live-grey-0);

  .roundrobin-grid__popup-item-number {
    width: 30px;
    font-size: 17px;
    font-weight: 500;
    color: var(--palette-live-grey-9);
  }

  .roundrobin-grid__popup-item-double {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .roundrobin-grid__popup-item-name {
    display: block;
    font-size: 17px;
  }
}

.roundrobin-grid__popup-button-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 15px;

  button {
    margin: 0;
  }

  button:first-child {
    margin: 0 5px 0 0;
  }

  button:last-child {
    margin: 0 0 0 5px;
  }
}