.venues__wrapper{
	height: 100vh;
	display: flex;
	flex-direction: column;

	.venues__block {
		height: 100%;
		padding: 0px 20px 70px 20px;
	
		.venues__name-container {
			display: flex;
			align-items: center;
			margin-right: 5px;
	
			.venues__number {
				font-size: 20px;
				margin: 0 15px 0 0;
			}
		}
	}
}

.novenues-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100%;
	padding: 15px 15px 70px 15px;

	.rockets-icon {
		fill: var(--palette-live-grey-6);
	}

	.novenues-title{
		font-weight: 600;
		font-size: 24px;
		text-align: center;
		margin: 10px;
	}

	.novenues-text {
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		max-width: 185px;
		margin: 10px;
	}
}