.tournament-form__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tournament-form__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tournament-form__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 674px;
  font-weight: 500;

  .tournament-form__title {
    font-size: 20px;
  }

  .tournament-form__icon-cross {
    cursor: pointer;
  }
}

.tournament-form__form {
  overflow: hidden;
}

.tournament-form__head {
  width: 95%;
  display: flex;
  align-items: center;
}