.checkbox__container {
	margin: 2px 0 25px 0;

	.checkbox__block {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;

		.checkbox-input {
			width: moz-fit-content;
			width: fit-content;
			border: 2px solid var(--palette-live-blue);
			border-radius: 9px;
			padding: 2px;

			.checkbox-element {
				cursor: pointer;
				position: absolute;
				z-index: -1;
				opacity: 0;
				margin: 0;
				padding: 0;
				box-sizing: border-box;

				& + label::before {
					content: "";
					display: flex;
					width: 22px;
					height: 22px;
					border-radius: 5px;
				}

				&:checked + label::before {
					background-color: var(--palette-live-blue);
				}
			}
		}

		.checkbox-label {
			color: var(--palette-live-grey-3);
			font-size: 14px;
			margin: 0 10px 0 15px;

			& a {
				color: var(--palette-live-blue);
				text-decoration: none;
			}
		}
	}

	.error-checkbox {
		color: var(--palette-live-red-1);
		font-size: 14px;
		margin: 10px 0 0 0;
	}
}