.back-button {
	width: 42px;
	height: 42px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100px;
	border: 1px solid var(--palette-live-grey-5);
	cursor: pointer;
	background-color: var(--palette-live-bg-1-white);

	.back-button__icon {
		width: 22px;
		height: 24px;
		margin: 0 0 0 7px;
		fill: var(--palette-live-grey-1);
		flex-shrink: 0;
	}
}