.stage-form__wrapper {
  width: 100%;
}

.stage-form__container {
  position: relative;
}

.stage-form__button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stage-form__button-clean {
  font-size: 19px;
  color: #4694D9;
  font-weight: 600;
  background-color: transparent;
  border: none;
}

.stage-form__participants-list-container {
  p {
    margin: 5px;
  }

  .stage-form__participants-list-doubles-raiting {
    font-size: 20px;
    margin-right: 15px;
  }
}

.stage-form__button-edition-mode {
  width: 35px;
  height: 35px;
  cursor: pointer;
}

.stage-form__level {
  padding-top: 15px;
}

.stage-form__level-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px 0;
}

.stage-form__level-name {
  font-weight: 500;
  font-size: 22px;
  margin: 10px 0;
}

.delete-icon {
  cursor: pointer;
}

.stage-form__group {
  margin-bottom: 20px;
  border: 1px solid #CCCCCC;
  border-radius: 9px;
  padding: 15px 15px 0 15px;
}

.stage-group__header {
  display: flex;
  align-items: center;
}

.stage-group__name {
  font-weight: 500;
  font-size: 19px;
  margin: 0 15px 15px 0;
}

.stage-group__type {
  box-sizing: border-box;
  height: 45px;
  width: 100%;
  border: 1px solid #E0E0E0;
  border-radius: 11px;
  line-height: 2.5;
  padding-left: 10px;
  font-size: 18px;
  color: #3A3A3A;
  margin: 5px 0 10px;
}

.stage-form__participants-list {
  padding-top: 20px;
}

.stage-form__participants-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.stage-form__participants-list-title {
  font-size: 20px;
  font-weight: 500;
  margin: 0;
}

.stage-form__participants-list-container {
  border: 1px solid #CCC;
  border-radius: 11px;
  margin-top: 10px;
}

.stage-form__participants-list-player {
  border-bottom: 1px solid #CCC;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-player--chosen {
  border: 3px solid #4694D9;
  font-size: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-player:first-child {
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-player:last-child {
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-player--chosen:first-child {
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-player--chosen:last-child {
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}


.stage-form__participants-list-container-doubles {
  padding: 15px;
}

.stage-form__participants-list-doubles {
  padding: 0px 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #CCCCCC;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  /* cursor: pointer; */
}

.stage-form__participants-list-doubles:first-child {
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-doubles:last-child {
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-doubles--chosen {
  padding: 10px 15px;
  display: flex;
  align-items: center;
  border: 3px solid #4694D9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.stage-form__participants-list-doubles--chosen:first-child {
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.stage-form__participants-list-doubles--chosen:last-child {
  border: none;
  border-bottom-left-radius: 11px;
  border-bottom-right-radius: 11px;
}

.stage-form__participants-list-doubles-name {
  font-size: 20px;
}

.stage-form__participants-list-doubles-name:first-child {
  margin-bottom: 5px;
}

.stage-form__button-level-addition {
  margin: 20px 0 30px 0;
}

.stage-form__button {
  box-sizing: border-box;
  width: calc(100vw - 15px);
  position: fixed;
  padding: 0 10px;
  bottom: 120px;
  left: 0px;
  right: 0px;
  margin: auto;
}

.stage-form__container-deletion {
  display: flex;
  justify-content: center;
  margin: 25px 0;
}

.stage-form__button-deletion {
  color: #FF0101;
  border: none;
  background-color: transparent;
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
}

.stage-form__confirmation {
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
}

.stage-form__confirmation-buttons {
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.stage-form__confirmation-buttons button {
  margin: 0;
  width: 50%;
}

.stage-form__confirmation-buttons button:first-child {
  margin-right: 5px;
}

.stage-form__confirmation-buttons button:last-child {
  margin-left: 5px;
}