.tournament-footer__container {
  background-color: #fff;
  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 15px;
  border-top: 1px solid var(--palette-live-grey-6);
}

.tournament__stages-menu{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow--down, .arrow--up {
  cursor: pointer;
  padding: 10px 0;
}

.tournament__footer-menu{
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}

.tournament__footer-menu--opened{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.tournament-footer__link {
  cursor: pointer;
}