.splash-screen__container {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: space-between;
	background-color: var(--palette-live-blue);

	.splash-screen__logo-container {
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;

		.splash-screen__logo {
			width: 187px;
			fill: var(--palette-live-bg-1-white);
		}
	}

	.splash-screen__button-container {
		display: flex;
		flex-direction: column;
		margin: 0 20px 0 20px;
	}

	.splash-screen__link {
		display: flex;
		align-items: center;
		justify-content: center;
		color: var(--palette-live-bg-1-white);
		font-size: 12px;
		font-weight: 400;
		margin: 16px 20px 16px 20px;
		cursor: pointer;
	}
}