.search__container{
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 20px 15px 20px 15px;
	border-radius: 12px;
	background-color: var(--palette-live-grey-8);

	.search__loupe{
		width: 22px;
		height: 22px;
		margin-right: 15px;
		fill: var(--palette-live-grey-6);
	}

	.search__input{
		border: none;
		font-size: 18px;
		width: 100%;
		background-color: var(--palette-live-grey-8);
		outline: none;
		padding: 0;
	}
}