.nocontent__container{
  padding: 100px 0;

  .nocontent__section {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .nocontent__title {
    font-size: 20px;
    margin: 0 0 0 0;
    font-weight: 500;
    text-align: center;
  }
  
  .nocontent__text{
    font-size: 19px;
    margin: 0 0 0 0;
    font-weight: 400;
  
  }
  
  .nocontent__paragraph{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
  
  .nocontent__icon{
    fill: var(--palette-live-grey-7);
    margin-right: 10px;
  }
}