.match__wrapper {
  width: 100%;
  height: max-content;

  .match__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: max-content;

    .match__participants {
      width: 100%;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid var(--palette-live-grey-3);

      .match__set-players__name--first,
      .match__set-players__name--second {
        font-size: 2.5dvh;
        text-align: center;
        width: 50%;
        padding: 1.5dvh 0.1dvh;
        margin: 0 0 10px 0;
      }

      .match__set-players__name--second {
        border-left: 1px solid var(--palette-live-grey-3);
      }
    }

    .match__sets {
      margin: 1.5dvh 0 3dvh 0;

      .match__set-wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 1dvh;

        .match__set-container {
          grid-column: 2;
          display: flex;
          justify-content: space-between;
          align-items: center;
          justify-self: center;
          padding: 0 3dvh;
          font-family: "Sawarabi Gothic";
          cursor: pointer;

          p {
            margin: 1.5dvh 0;
          }

          .match__set-score,
          .match__set-score--inactive {
            font-size: 3dvh;
            padding: 0 2.5dvh;
            color: var(--palette-live-blue);
            text-align: center;
            min-width: 30px;
            position: relative;
          }

          .match__set-score--inactive {
            color: var(--palette-live-grey-0);
          }

          .match__set-tiebreak,
          .match__set-tiebreak--inactive {
            position: absolute;
            margin: 0;
            font-size: 2dvh;
            color: var(--palette-live-blue);
            padding-top: 5px;
          }

          .match__set-tiebreak--inactive {
            color: var(--palette-live-grey-0);
          }
        }

        .match__set-icon-deletion {
          grid-column: 3;
          align-self: center;
          cursor: pointer;
          height: 2dvh;
        }
      }
    }

    .match__buttons {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;

      .match__button-set-addition {
        border: none;
        background-color: var(--palette-live-bg-0-white);
        color: var(--palette-live-grey-9);
        font-size: 2.3dvh;
        margin-bottom: 2.5dvh;
        cursor: pointer;
      }

      .match__button-tiebreak {
        font-size: 2.1dvh;
        border: 1px solid var(--palette-live-blue);
        padding: 1.5dvh 5dvh;
        border-radius: 50px;
        margin-bottom: 25px;
        cursor: pointer;
      }
    }
  }


  .match__swipers {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    font-family: "Sawarabi Gothic";
    max-width: 70vw;
    margin-bottom: 3dvh;

    .match__separator {
      position: absolute;
      top: 0px;
      height: 100%;
      left: 50%;
      width: 1px;
      background-color: var(--palette-live-grey-7);
    }

    .swiper {
      width: 70vw;
      height: 25dvh;
      overflow: hidden;
      overflow-x: auto;
      position: relative !important;
      box-sizing: border-box;
      display: flex;

      .swiper-wrapper{
        display: flex;
        flex-direction: column;
        margin: 0 10px;
      }

      .swiper-slide {
        text-align: center;
        font-size: 3dvh;
        color: var(--palette-live-grey-3);
        height: 5dvh !important;
        width: 50px;

        display: flex;
        justify-content: center;
        align-items: center;
      }

      .swiper-slide-active   {
        font-size: 4dvh;
        color: var(--palette-live-grey-0);
      }

      .swiper-slide-next,
      .swiper-slide-prev {
        font-size: 3.5dvh;
        color: var(--palette-live-grey-7);
      }
    }

    .swiper--left {
      justify-content: flex-end;

      .swiper-button-prev {
        position: absolute;
        top: 25%;
        cursor: pointer;
        height: 40px;
        width: 40px;
        display: flex;
        margin-right: 70px;
        justify-content: center;
        align-items: center;

        &::before {
          content: "";
          display: inline-block;
          height: 2px;
          width: 3dvh;
          background-color: var(--palette-live-grey-7);
        }
      }

      .swiper-button-next {
        position: absolute;
        bottom: 25%;
        cursor: pointer;
        height: 40px;
        width: 40px;
        margin-right: 70px;
        display: flex;
        justify-content: center;
        align-items: center;

        &::before {
          content: "";
          display: inline-block;
          width: 5dvh;
          height: 5dvh;
          background:
            linear-gradient(var(--palette-live-grey-7), var(--palette-live-grey-7)),
            linear-gradient(var(--palette-live-grey-7), var(--palette-live-grey-7));
          background-position: center;
          background-size: 50% 2px, 2px 50%;
          background-repeat: no-repeat;
        }
      }

      .swiper-wrapper {
        position: absolute !important;
        right: 0px;
        cursor: pointer;
        align-items: flex-end;
      }
    }

    .swiper--right {
      justify-content: flex-start;

      .swiper-button-prev {
        position: absolute;
        top: 25%;
        cursor: pointer;
        height: 40px;
        width: 40px;
        display: flex;
        margin-left: 70px;
        justify-content: center;
        align-items: center;

        &::before {
          content: "";
          display: inline-block;
          height: 2px;
          width: 3dvh;
          background-color: var(--palette-live-grey-7);
        }
      }

      .swiper-button-next {
        position: absolute;
        bottom: 25%;
        cursor: pointer;
        height: 40px;
        width: 40px;
        margin-left: 70px;
        display: flex;
        justify-content: center;
        align-items: center;

        &::before {
          content: "";
          display: inline-block;
          width: 5dvh;
          height: 5dvh;
          background:
            linear-gradient(var(--palette-live-grey-7), var(--palette-live-grey-7)),
            linear-gradient(var(--palette-live-grey-7), var(--palette-live-grey-7));
          background-position: center;
          background-size: 50% 2px, 2px 50%;
          background-repeat: no-repeat;
        }
      }

      .swiper-wrapper {
        position: absolute !important;
        left: 0px;
        cursor: pointer;
        align-items: flex-start;
      }
    }
  }
}