.profile__container{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-radius: 12px;
	padding: 23px 20px 23px 20px;
	background-color: #f6fbff;
	border: 1px solid var(--palette-live-grey-4);
	margin: 0 20px 0 20px;

	.profile__photo {
		width: 120px;
		height: 120px;
		border-radius: 100px;
		object-fit: cover;
	}

	.profile__photo-letters{
		font-size: 30px;
		font-weight: 500;
		border-radius: 100px;
		background-color: #ddecff;
		padding: 40px;
		margin: 0;
	}

	.profile__subtitle{
		font-size: 20px;
		font-weight: 500;
		margin: 12px 0 0 0;
	}
}