.stage-creation{
  width: 100%;
  margin-bottom: 90px;
}

.stage-creation__participants {
  color: #3A3A3A;
  font-size: 19px;
  font-weight: 400;
  margin: 0px 0 25px 0;
}

.stage-creation__input, .stage-creation__input--checked {
  border: 1px solid #aeadad;
  border-radius: 11px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px;
}

.stage-creation__input--checked {
  background-color: #F5FAFF;
}

.stage-creation__groups {
  margin: 30px 0 30px 0;
}

.stage-creation__counter{
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.stage-creation__button-counter {
  background-color: #EBF4FF;
  border: 1px solid #367BCF;
  color: #367BCF;
  border-radius: 11px;
  padding: 10px;
  width: 25%;
  font-size: 25px;
}

.stage-creation__counter-input{
  box-sizing: border-box;
  font-size: 20px;
  text-align: center;
  margin: 0 10px;
  border: 1px solid #aeadad;
  border-radius: 11px;
  width: 50%;
}

.form-submit--manual-formation{
  margin: 20px 0 0 0;
}