.tournaments__container {
	min-height: 100vh;
	display: flex;
	flex-direction: column;

	.tournaments__content {
		height: 100%;
		overflow: auto;
		padding: 0 20px 80px 20px;

		.tournaments__plug {
			height: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}

		.tournaments__plug-title {
			text-align: center;
			margin: 10px;
		}

		.tournaments__plug-text {
			text-align: center;
			font-size: 18px;
			margin: 10px;
		}

		.change-view__container {
			background-color: var(--palette-live-bg-0-white);
			padding: 12px;
			font-size: 14px;
			font-weight: 400;
			color: var(--palette-live-grey-0);
			border: 1px solid var(--palette-live-grey-5);
			border-radius: 100px;
			margin: 0 0 20px 0;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}

	.tournaments__creation{
		padding: 0 20px 80px;
	}
}

.tournaments__button-addition {
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	right: 20px;
	bottom: 80px;
	border-radius: 100%;
	width: 50px;
	height: 50px;
	background-color: #4694D9;
	cursor: pointer;
}