.edit-button {
	width: 42px;
	height: 42px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100px;
	border: 1px solid var(--palette-live-grey-5);
	cursor: pointer;
	background-color: var(--palette-live-bg-1-white);

	.edit-button__icon {
		width: 30px;
		height: 30px;
		margin: 3px 0 0 3px;
    fill: var(--palette-live-grey-1);
    flex-shrink: 0;
	}
}