.block__container{
	padding: 15px;
	background-color: var(--palette-live-bg-1-white);
	border: solid var(--palette-live-grey-5);
	border-top-width: 0;
	border-bottom-width: 0;
	border-left-width: 0;
	border-right-width: 0;

	.block__label{
		display: block;
		font-size: 16px;
		color: var(--palette-live-grey-3);
		margin: 0 0 15px 0;
	}

	.block__content{
		display: block;
		font-size: 18px;
		color: var(--palette-live-grey-0);
	}
}