.match-group__container {
  margin: 0 0 20px 0;
}

.match-group__title {
  color: var(--palette-live-grey-0);
  font-size: 20px;
  font-weight: 500;
}

.match-table__container {
  border-radius: 10px;
  display: table;
  width: 100%;
  margin: 0 0 40px 0;
  box-sizing: border-box;
  border: 1px solid var(--palette-live-grey-6);
  overflow: hidden;
}

.match-table__row {
  display: table-row;
  width: 100%;
  white-space: nowrap;
  box-sizing: border-box;
}

.match-table__cell {
  display: table-cell;
  border-bottom: 1px solid var(--palette-live-grey-6);
  color: var(--palette-live-grey-0);
  font-size: 17px;
  font-weight: 400;
  vertical-align: middle;
  position: relative;
  text-align: left;
  min-width: 35px;
  width: 100%;
  box-sizing: border-box;
}

.match-table__cell:first-child {
  border-right: 1px solid var(--palette-live-grey-6);
}

.match-table__row:last-child .match-table__cell {
  border-bottom: none;
}

.match-table__cell-names {
  padding: 10px 15px 10px 10px;

  p {
    margin: 0;
  }

  p:first-child {
    margin-bottom: 10px;
  }
}

.cell-order{
  width: 35px;
}

.match-table__cell-order{
  width: 100%;
  text-align: center;
  margin: 0 0 5px 0;
}

.match-table__cell-ball{
  text-align: center;
}

.match-table__cell-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}

.match-table__cell-sets{
  padding-right: 5px;
}

.match-table__cell-sets-score {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 0 10px 0;
}
.match-table__cell-sets-score:last-child {
  border: 0;
  padding: 0;
}
.match-table__cell-sets-score > p {
  margin: 0 5px 0 0;
  cursor: pointer;
  min-width: 37px;
}
.match-table__cell-sets-score > p:last-child {
  margin: 0;
}
.match-table__cell-sets-score > p > span {
  color: var(--palette-live-blue);
}

.match-group__notable{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  .match-group__notable-group-name{
    font-weight: 500;
    font-size: 20px;
    margin: 0  0 15px 0;
  }

  .match-group__notable-group-text{
    font-size: 18px;
    padding: 15px;
    border: 1px solid var(--palette-live-grey-6);
    width: 100%;
    box-sizing: border-box;
    border-radius: 13px;
    margin: 0  0 15px 0;;
  }
}