.players__wrapper{
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.noplayers__container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100%;
	padding: 15px 15px 70px 15px;

	.rockets__icon {
		fill: var(--palette-live-grey-6);
	}

	.noplayers__title{
		font-weight: 600;
		font-size: 24px;
		text-align: center;
		margin: 10px;
	}

	.noplayers__text {
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		max-width: 185px;
		margin: 10px;
	}
}

.search__container {
	margin: 0 20px 0 20px;
}

.players__list {
	height: 100%;
	margin: 20px 20px 65px 20px;
	overflow: auto;

	.players__name-container {
		display: flex;
		align-items: center;
		margin-right: 5px;

		.players__number {
			font-size: 20px;
			margin: 0 15px 0 0;
		}
	}
}